import { InstantSearch } from 'react-instantsearch/dom'

/* eslint prefer-destructuring: 0 */
const ALGOLIA_APP_ID = process.env.ALGOLIA_APP_ID || 'MNBZ23PSLG'
const ALGOLIA_API_KEY = process.env.ALGOLIA_API_KEY || '73cf9c984525e3e7d4d6a29398898140'
const ALGOLIA_INDEX_NAME = process.env.ALGOLIA_INDEX_NAME || 'movie_db'

const withInstantSearch = Component => props => (
  <InstantSearch appId={ALGOLIA_APP_ID} apiKey={ALGOLIA_API_KEY} indexName={ALGOLIA_INDEX_NAME}>
    <Component {...props} />
  </InstantSearch>
)

export default withInstantSearch



